*{
    padding:0;
    margin:0;
}

.Nav-Parent{
    top:0;
    width:100%;
    background-color: rgba(0, 0, 0, 0.807);
    color:white;
    position: sticky;
}

.Nav-Child{
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding:0.5cm 1cm;
    border-bottom: thin solid rgb(58, 58, 58);
}

.Menu-Parent{
    width:fit-content;
}

.Menu-Child{
    display: flex;
    flex-direction: row;
    column-gap: 0.5cm;
    align-items:center;
}