.img{
    padding:0;
}

.heading1{
    font-size: 48px;
    font-weight: 600;
  }

.heading2{
    font-size: 24px;
}

.heading3{
    font-size: 16px;
}

.heading4{
    font-size: 14px;
}

.homeParent {
    width: 100%;
  }

.homeDivisions{
    display: flex;
    flex-direction: column;
    row-gap: 0.8cm;
}
  
.background-image {
    top:0;
    position: relative;
    width: 100%;
    height: 75vh;
    background-image: url("background-home.jpg");
    background-size: cover;
    background-position: center;
}
  
.homeChild1P{
    height:100%;
    width:100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.homeChild1 {
    color: white;
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    width:70%;
    row-gap: 0.8cm;
  }
  
  .btn1 {
    font-size: 18px;
    color:white;
    border:5px solid rgb(106, 43, 10);
    background-color: transparent;
    padding:0.3cm 0.8cm;
    border-radius: 12px;
    
  }
  
  .btn1:hover{
    background-color: rgb(106, 43, 10);
  }

  .heading1{
    font-size: 48px;
    font-weight: 600;
  }

  .heading3{
    font-size: 16px;
  }

.footerP{
    color:white;
    background-color: black;
    width:100%;
    bottom:0;
    height: fit-content;
}

.footerC{
    display: flex;
    flex-direction: column;
    row-gap:0.5cm;
    padding:1cm;
}

.footerC1{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.col1Footer{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.homeChild2P{
    width:100%;
}

.homeChild2{
    display: flex;
    flex-direction: row;
    column-gap:0.8cm;
    padding:2cm;
}

.card1{
    color:aliceblue;
    display: flex;
    flex-direction: row;
    align-items: center;
    width:8cm;
    border-radius: 12px;
    /* border:thin solid grey; */
    background-color: rgb(30, 30, 30);
    box-shadow: 0.5px 0.5px rgba(52, 52, 52, 0.475);
    padding:0.5cm 1.5cm 0.2cm 0.0cm;
}

.cardCol1{
    padding:-0.5cm;
    width:4cm;
}
.imageProfile{
    width:60%;
    height:auto;
    border-radius: 50%;
}

.cardCol2{
    width:4cm;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    row-gap: 0.3cm;

}

.cardCol2Child{
    display: flex;
    flex-direction: row;
    column-gap: 1cm;
    align-items: center;
}

.btn2{
    font-size: 14px;
    color:white;
    border:5px solid rgb(134, 53, 9);
    background-color: transparent;
    padding:0.1cm 0.5cm;
    border-radius: 18px;
    
  }
  
  .btn2:hover{
    background-color: rgb(134, 53, 9);
  }
